import React from 'react';
import PropTypes from 'prop-types';

import './style';

const Footer = (props, context) => {
  const { theme: { colorPrimary, textPrimary } } = context;
  return (
    <footer>
      <div className="content-grid">
        <div style={{ color: textPrimary }}> 2019 Customised by <a href="https://github.com/axelginepro" target="_blank" rel="noopener noreferrer" style={{ color: colorPrimary }}>Axel Ginepro (inspired by a dev from github)</a></div>
      </div>
    </footer>
  );
};

Footer.contextTypes = {
  theme: PropTypes.any,
  switchTheme: PropTypes.func
};

export default Footer;


