import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import './style.scss';
import Slide from 'react-reveal/Slide';
import Typing from 'react-typing-animation';

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
      <Slide right>
        <h1 style={{ color: colorPrimary }}>About</h1>
      </Slide>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              Passionn d 'informatique depuis les annes 90, je me suis form pour approfondir mes connaissances et comptences dans le langage JavaScript :  <span className="highlight">React.js / Node.js / React.native</span> dans une formation intensive "La Capsule bootcamp"  Lyon.
            </p>
            <p>
              En reconversion professionnelle, j'ai travaill dans l'hotellerie de luxe durant 15 annes dans le domaine du Bar, ayant le got pour la rigueur et l'excellence. {' '}
              Des tablissements comme : {' '}
              <span className="highlight">Hotel Martinez Cannes, Mandarin Oriental Paris / Genve et le Royal Mansour Marrakech</span>.
              
            </p>
            <p>
              A la recherche d'une entreprise pour une 1er exprience en tant que developpeur web react.js / react.native <span className="highlight"> Lyon</span>.
              Passionn et curieux, j'aime dcouvrir et tester de nouveaux concepts, de nouvelles technologie, voulant me perfectionner dans les diffrents langages que j'utilise actuellement : <div> 
              <Typing speed={200}><i>React-Redux, Node.js, React.native, Express.js, MongoDB.</i></Typing></div>
            </p>
            <Typing speed={50}>
            <Typing.Delay ms={3000} />
            <p>
              Actuellement disponible pour des futurs entretiens.
            </p>
            </Typing>
            <p className="text-emoji" style={{ color: colorPrimary }}>
            </p>
            PS : Merci  Nol, Benjamin et Alexis @ La Capsule academy
          </div>
        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio-page" />
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



